import React, {useState, Fragment} from 'react';
import ContactMe from './contact.js';
import { Button } from 'react-bootstrap';
import {FormattedMessage} from 'react-intl';

export default function Contact(){
	
 	const [newForm, setNewForm] = useState(true);
	const showForm = newForm ? "new-form" : "new-form show-form";

	return (
	<>
		<Button onClick={() => setNewForm(!newForm)}>
			<FormattedMessage id="contact.me" defaultMessage="Contact me " />{" "}
			<br/><i className="fas fa-chevron-down"></i>
		</Button>

		<ContactMe showForm={showForm} newForm={newForm} setNewForm={setNewForm}/>
	</>
);
}